<template>
  <div class="home">
     PayOuts
    <div>
      <b-button v-b-modal.modal-1>Launch demo modal</b-button>
      <b-modal id="modal-1" :title="`send payment to ${reciever.name}`" hide-footer>
          <form method="post" @submit.prevent="paymentComplete()">
            <p class="alert alert-success" v-if="success">{{ this.success.status  }} / {{  this.success.payout_batch_id }}</p>
            <p class="alert alert-danger" v-if="errors">{{ this.errors.status  }}</p>
            <div>
                <p class="my-4" style="color:red">
                  1 -  Enter The amount that you would like to send
                </p>
                <b-form-input v-model="fields.amount" ></b-form-input>
            </div>

            <div>
                <p class="my-4" style="color:red">
                  2 -  Select Payment method
                </p>
                <b-container fluid class="p-4">
                    <b-row>
                        <b-col class="container-image-payment"  @click="fields.payment_method = 'stripe' " :style="{ 'background-color' : fields.payment_method == 'stripe' ? '#eee' : 'white' }">
                          <b-img thumbnail fluid :src="require('../assets/images/stripe.png')" alt="Image 2"></b-img>
                        </b-col>
                        <b-col class="container-image-payment" @click="fields.payment_method = 'paypal' " :style="{ 'background-color' : fields.payment_method == 'paypal' ? '#eee' : 'white' }">
                          <b-img thumbnail fluid :src="require('../assets/images/paypal-logo.png')" alt="Image 3"></b-img>
                        </b-col>
                    </b-row>
                </b-container>
            </div>

            <div >
                <p class="my-4" style="color:red">
                    3 -  Select Payment Type
                </p>
                <b-form-group class="container-toggle"
                    v-slot="{ ariaDescribedby }"
                  >
                      <b-form-radio-group
                        id="btn-radios-2"
                        v-model="fields.payment_type"
                        :options="options"
                        :aria-describedby="ariaDescribedby"
                        button-variant="outline-success"
                        size="sm"
                        name="radio-btn-outline"
                        buttons
                      ></b-form-radio-group>
                </b-form-group>

            </div>
            <div class="container-button-submit-form">
                <b-button type="submit" pill class="button-form-submit">Complete Payment</b-button>
            </div>
      </form>
        </b-modal>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
	name: "Home",
	data() {
		return{
			plaid: null,
			errors: null,
			success: null,
			reciever: {
				name: 'jEIF recievier',
				id: 1,
				email: 'receiver@example.com'
			},
			sender: {
				name: 'jEIF sender',
				id: 2
			},
			fields: {
				amount: 0,
				payment_type: 'Full-Payment',
				payment_method: 'paypal',
				sender_id: null,
				reciever_id: null,
				email_reciever: null
			},
			options: [
				{ text: 'Full Payment', value: 'Full-Payment' },
				{ text: 'Deposit', value: 'Deposit' }
			]
		}
	},
	methods: {
		paymentComplete () {
			let self = this;
			this.fields.sender_id      = this.sender.id
			this.fields.reciever_id    = this.reciever.id
			this.fields.email_reciever = this.reciever.email

			this.axios.get('api/get-link-token', this.fields).then(({ data }) => {
				this.plaid = Plaid.create({
					token: data.link_token,
					onSuccess: (public_token, metadata) => {
						console.log({
							public_token,
							metadata
						});

						self.accessToken(metadata);
					},
					onLoad: () => {
						console.log({ from: 'onLoad' })
					},
					onExit: (err, metadata) => {
						console.log({ err, metadata })
					},
					onEvent: (eventName, metadata) => {
						console.log({ eventName, metadata })
					},
					receivedRedirectUri: null,
				})

				this.plaid.open()
			}).catch(({ response }) => {
				this.errors = response
				console.log({ errors: this.errors })
			})
		},
		accessToken(metadata){
			console.log('hihi');
            this.axios.post('api/create-access-token', metadata).then(({ data }) => {
               console.log(data);
			}).catch(({ response }) => {
                this.errors = response
				console.log({ errors: this.errors })
			});
		}
	}
}
</script>
